.Tile {
  background: gray;
  overflow: hidden;
  height: 300px;
  width: auto;
  margin: 10px;
  padding-left: 30px;
}

.Info {
  margin: 10px;
}

.Line {
  border-left: 6px solid green;
  height: 500px;
  position: absolute;
  padding-left: 5px;
  top: 0;
}

.Highlight {
  margin-bottom: 15px;
  background-color: rgb(224, 224, 224);
}
